/* '#a1c3d1',  '2B4141', '861657', 'F7B2B7', '7F2982', '7F7EFF' */
.content {
  padding-left: 9vw;
  padding-right: 9vw;
  width: 80vw;
  margin-bottom: 15vh; }

h1,
h4 {
  color: #E84398;
  display: inline-block; }

h1.expansive_bois {
  margin-top: 33vh;
  margin-bottom: 15vh;
  width: min-content;
  /* max-width: 45vw; */ }
  h1.expansive_bois ~ .timespan {
    bottom: 15vh;
    color: #E84398;
    display: flex;
    position: relative;
    font-size: 5rem; }

h4.expansive_bois {
  display: block;
  margin: 0;
  width: 45vw;
  font-weight: 600; }

.expansive_bois:nth-child(even) {
  text-align: right;
  margin-left: auto; }

/* Narrow */
@media screen and (max-width: 768px) {
  .expansive_bois:nth-child(even) {
    text-align: unset;
    margin-left: unset; }
  h1.expansive_bois {
    font-size: 17rem; }
    h1.expansive_bois ~ .timespan {
      font-size: 4rem; }
  h4.expansive_bois {
    font-size: 4rem; }
  figure.expansive_bois {
    margin: auto !important;
    width: 75%; }
  h4.expansive_bois:nth-of-type(even) {
    text-align: right;
    margin-left: auto; } }

/* Mid */
@media screen and (min-width: 769px) and (max-width: 1365px) {
  h1.expansive_bois {
    font-size: 13rem; }
    h1.expansive_bois ~ .timespan {
      font-size: 3rem; }
  h4.expansive_bois {
    font-size: 3rem; } }

/* Mid-wide */
@media screen and (min-width: 1366px) and (max-width: 1920px) {
  h1.expansive_bois {
    font-size: 11rem; }
    h1.expansive_bois ~ .timespan {
      font-size: 2.5rem; }
  h4.expansive_bois {
    font-size: 2.5rem; } }

/* Wide */
@media screen and (min-width: 1921px) {
  h1.expansive_bois {
    font-size: 10rem; }
    h1.expansive_bois ~ .timespan {
      font-size: 2rem; }
  h4.expansive_bois {
    font-size: 2rem; } }

.content a:not(.arrow_link) {
  /* color: $highligth_color; */
  color: inherit;
  position: relative;
  white-space: nowrap; }
  .content a:not(.arrow_link)::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(/images/waaave.svg);
    background-position: 5px 1.1em;
    background-repeat: repeat-x;
    background-size: 15px 5px;
    padding-bottom: 0.5em;
    transition: background-position 1s linear;
    filter: invert(53%) sepia(80%) saturate(2994%) hue-rotate(216deg) brightness(101%) contrast(102%); }
  .content a:not(.arrow_link):hover {
    /* color: #282960; */ }
    .content a:not(.arrow_link):hover::after {
      animation-name: link;
      animation-duration: 0.5s;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
      /* filter: invert(12%) sepia(32%) saturate(3931%) hue-rotate(226deg)
          brightness(94%) contrast(91%); */ }

.arrow_link {
  color: #7F7EFF;
  font-size: 3.5rem; }

@keyframes link {
  from {
    background-position: 5px 1.1em; }
  to {
    background-position: 20px 1.1em; } }

figure {
  margin: 0;
  width: 66%;
  margin-bottom: 1vh; }

figure > img {
  width: 100%;
  border-radius: 2%;
  box-shadow: 2px 2px 15px 0px rgba(0, 0, 0, 0.5); }

img ~ figcaption {
  font-size: 3.5rem;
  color: #E84398;
  font-weight: 500; }
  @media screen and (min-width: 768px) {
    img ~ figcaption {
      font-size: 2rem; } }

.other_exps .other_exps_title {
  font-size: 5em;
  font-weight: 700;
  color: #E84398;
  display: flex;
  margin: auto !important;
  margin-top: 15vh !important;
  margin-bottom: 5vh !important;
  width: min-content;
  height: min-content;
  text-align: center !important; }
  @media screen and (max-width: 768px) {
    .other_exps .other_exps_title {
      font-size: 8em; } }

.other_exps .other_exps_items {
  width: 100%;
  display: grid;
  gap: 5%;
  grid-template-columns: 1fr 1fr; }
  .other_exps .other_exps_items .expansive_bois {
    align-self: center;
    width: 100%; }
    .other_exps .other_exps_items .expansive_bois .description {
      display: block;
      margin-top: 10px; }
    .other_exps .other_exps_items .expansive_bois .technologies {
      display: block;
      font-size: 0.7em;
      margin-top: 10px; }
    .other_exps .other_exps_items .expansive_bois .timespan {
      display: block;
      font-size: 0.7em;
      margin-top: 10px; }
  .other_exps .other_exps_items .expansive_bois:nth-child(1) {
    text-align: right;
    margin: 0; }
  .other_exps .other_exps_items .expansive_bois:nth-child(2) {
    text-align: left;
    margin: 0; }
