/* '#a1c3d1',  '2B4141', '861657', 'F7B2B7', '7F2982', '7F7EFF' */
:root {
  --disc-number: 8;
  --size: 100px; }

.menu-container {
  width: 100px;
  height: 100px;
  position: fixed;
  top: 15px;
  right: 15px; }

nav.menu {
  display: block;
  width: calc(var(--disc-number) * var(--size));
  height: calc(var(--disc-number) * var(--size));
  -webkit-transform: translate3d(calc(var(--size) / (var(--disc-number))), calc(-1 * var(--size) / (var(--disc-number))), 0);
  transform: translate3d(calc(var(--size) / (var(--disc-number))), calc(-1 * var(--size) / (var(--disc-number))), 0);
  transition: transform 0.3s cubic-bezier(0.3, 1.4, 0.5, 0.9), -webkit-transform 0.3s cubic-bezier(0.3, 1.4, 0.5, 0.9); }

nav.menu {
  position: relative;
  bottom: calc(calc(var(--size) * (var(--disc-number) / 2)) + -62px);
  right: calc(calc(var(--size) * (var(--disc-number) / 2)) + -36px);
  visibility: hidden;
  transition: visibility 0s step-end 0.7s; }

.open.menu {
  visibility: visible;
  transition: visibility 0s step-start 0s; }

a {
  color: #f0ebf4;
  text-decoration: none; }

.disc {
  position: absolute;
  display: inline-block;
  text-align: center;
  cursor: pointer;
  font-size: 25px;
  font-family: inherit;
  line-height: 40px;
  border-radius: calc(var(--disc-number) / 2 * var(--size));
  -webkit-transform: scale3d(0.5, 0.5, 0.5) rotate3d(0, 0, 1, 190deg);
  transform: scale3d(0.5, 0.5, 0.5) rotate3d(0, 0, 1, 190deg);
  pointer-events: none;
  opacity: 0;
  cursor: pointer;
  transition: opacity 0.3s, -webkit-transform 0.3s cubic-bezier(0.3, 1.4, 0.5, 0.9);
  transition: transform 0.3s cubic-bezier(0.3, 1.4, 0.5, 0.9), opacity 0.3s, -webkit-transform 0.3s cubic-bezier(0.3, 1.4, 0.5, 0.9); }

.disc div {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
  color: #f0ebf4; }
  .disc div img {
    width: 100%; }

.open .disc {
  pointer-events: auto;
  opacity: 1; }

.disc:hover a {
  color: inherit; }

