/* Last Level */
.last {
  top: calc((var(--disc-number) - 1) * var(--size) / (var(--disc-number) / 4));
  left: calc((var(--disc-number) - 1) * var(--size) / (var(--disc-number) / 4));
  right: calc((var(--disc-number) - 1) * var(--size) / (var(--disc-number) / 4));
  bottom: calc((var(--disc-number) - 1) * var(--size) / (var(--disc-number) / 4));
  background: #7F7EFF;
  -webkit-transition-delay: 0.3s;
  transition-delay: 0.3s;
  visibility: visible; }
  .last svg {
    position: relative;
    width: 33%;
    top: 7px;
    filter: invert(93%) sepia(2%) saturate(1637%) hue-rotate(210deg) brightness(104%) contrast(91%); }

.open .last {
  -webkit-transition-delay: 0.3s;
  transition-delay: 0.3s;
