/* ********************* */
.toggle {
  line-height: 100px;
  padding: 0;
  width: 100px;
  background: #E84398;
  opacity: 1;
  -webkit-transform: none;
  transform: none;
  pointer-events: auto;
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
  color: #440c47; }

.open .toggle {
  -webkit-transform: rotate3d(0, 0, 1, 0deg);
  transform: rotate3d(0, 0, 1, 0deg); }

.toggle:hover {
  background: #7F7EFF;
  color: #7F7EFF; }
  .toggle:hover svg {
    fill: #7F7EFF; }

.toggle:active {
  background: #F7B2B7;
  color: rgba(255, 215, 0, 0.5);
  -webkit-transform: scale(0.9);
  transform: scale(0.9);
  -webkit-transition-duration: 0s;
  transition-duration: 0s; }
