.exps_title {
  font-size: 7em;
  font-weight: 700;
  color: #E84398;
  display: flex;
  margin: auto;
  margin-top: 20vh;
  margin-bottom: 20vh;
  width: min-content;
  height: min-content;
  text-align: center; }
  @media screen and (max-width: 768px) {
    .exps_title {
      font-size: 10em; } }

.stagger_container {
  height: fit-content;
  display: grid;
  gap: 6.66vh; }
  .stagger_container .expansive_bois .description {
    display: block;
    margin-top: 10px; }
  .stagger_container .expansive_bois .technologies {
    display: block;
    font-size: 0.7em;
    margin-top: 15px; }
  .stagger_container .expansive_bois .timespan {
    display: block;
    font-size: 0.7em;
    margin-top: 15px; }

.exps_title ~ .stagger_container .expansive_bois a {
  font-size: 2em; }
