  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background: #7F2982;
  -webkit-transition-delay: 0s;
  transition-delay: 0s; }

.open-professional .l1-professional {
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
