  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background: #7F2982;
  -webkit-transition-delay: 0s;
  transition-delay: 0s; }

.open-personal .l1-personal {
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
  -webkit-transform: scale3d(1, 1, 1) rotate3d(0, 0, 1, 190deg);
  transform: scale3d(1, 1, 1) rotate3d(0, 0, 1, 190deg);
  opacity: 1; }

.open-personal .l1-personal:hover {
