.RepulsionHandler {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: absolute; }

#no_balls_button {
  width: 100px;
  height: 100px;
  border-radius: 50px;
  border: none;
  background-color: #E84398;
  color: #f0ebf4;
  cursor: pointer;
  font-size: 25px;
  font-family: inherit;
  outline: none;
  position: relative;
  top: 15px;
  left: 15px;
  font-size: 25px; }
  #no_balls_button:hover {
    background-color: #7F7EFF; }
  #no_balls_button:active {
    background-color: #F7B2B7;
    transform: scale(0.9, 0.9); }
